body{
  font-family: 'Arial';
}

.container{
  max-width: 500px;
  margin: 0 auto;
}


.container {
}
.popup {
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.11);
  position: relative;
  // .popup__inner

  &__inner {
    position: absolute;
    width: 300px;
    height: 150px;
    background-color: #fff;
    margin: 0 auto;
    left: 0;
    right: 0;
    top: 200px;
  }

  // .popup__title

  &__title {
    text-align: center;
  }

  // .popup__inner-quest

  &__inner-quest {
    position: absolute;
    width: 300px;
    height: 150px;
    background-color: #fff;
    margin: 0 auto;
    left: 0;
    right: 0;
    top: 200px;
    display: none;
    &.active{
      display: block;
    }
  }

  // .popup__del

  &__del {
    cursor: pointer;
    img{
      margin-left: auto;
    }
  }

  // .popup__qustion

  &__qustion {
    text-align: center;
  }

  // .popup__button-inner

  &__button-inner {
    display: flex;
    justify-content: space-between;
    margin-top: 33px;
    padding: 0 7px;
    button{
      width: 80px;
      height: 30px;

    }
  }
  &__main-but {
    width: 95px;
    height: 35px;
    display: block;
    margin: 0 auto;
    margin-top: 50px;
  }

  &__quest-but{
    
  }
}
.popup {

  // .popup__inner-yas

  &__inner-yas {
    position: absolute;
    width: 300px;
    height: 150px;
    background-color: #fff;
    margin: 0 auto;
    left: 0;
    right: 0;
    top: 200px;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    display: none;
    &.active{
      display: flex;
    }
  }

  // .popup__yas-title

  &__yas-title {
    font-size: 30px;
  }
}

